//Button  style   

import { darken } from "polished";
import { css, styled } from "styled-components";

//
//  hover
const colorStyles = css`
  ${() => {
    const selected = '#228be6';
    return css`
      background: ${selected};
      &:hover {
        background: ${darken(0.3, selected)};
      }
      &:active {
        background: ${darken(0.3, selected)};
      }
    `;
  }}
`;
const StyledButton = styled.button`
  // display: inline-flex;
  color: white;
  font-weight: bold;
  outline: none;
  border-radius: 4px;
  border: none;
  cursor: pointer;

  //
  width : 40%;
  min-width: 15px;
  max-width: 170px;

  //   
  height: 2.25rem;
  line-height : 2.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;

  //
  text-align: center;
  white-space : nowrap;

  /*   */
  ${colorStyles}

  //  
  position : relative;
  z-index : 4;

  & + & {
    margin-left : 2rem;
  }
  `

export default function Button({children,color, onClick}){
  return( 
      <StyledButton color={color} onClick={onClick}>
        {children}
      </StyledButton>
  );
}