//
import "../App.css";
import "../Layout.css";
import styled from "styled-components";

import Calendar from "react-calendar"; // 
import "../components/Calendar.css"; //  css
import SplitScreen from "../SplitScreen"; // 

import Account from "../components/Account"; // 
import TotalAccount from "../components/TotalAccount"; //
import Schedule from "../components/Schedule"; //

import Button from "../components/Button"; //
import Modal from "../components/Modal"; //
import AccountModal from "../components/EditModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { jwtDecode } from "jwt-decode";

//
import { React, useEffect, useState } from "react";
import moment from "moment";
import { useNavigate } from "react-router-dom";
import ButtonList from "../components/ButtonList";

//  css
export const ButtonGroup = styled.div`
  display: flex;
  justify-content: space-around;
  margin-top: 1rem;
`;

function AppHeader({ userData }) {
  const navigate = useNavigate();

  //  
  function onLogout() {
    localStorage.removeItem("accessToken");
    navigate("/mohani/");
    // window.location.reload(true);
  }

  // console.log(userData)

  const logoUrl = "LogoTrans.png";

  return (
    <>
      <div className="header">
        <span className="header_name">{userData.username} , !</span>
        <img className="header_Logo" src={process.env.PUBLIC_URL + "/" + logoUrl} />
        <div className="headerLogoutWrap">
          <button className="header_logout" onClick={onLogout}>
            
          </button>
        </div>
      </div>
    </>
  );
}

const LeftComponent = ({ title, onChange, value, scheduleData}) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const openModal = () => {
    setIsModalOpen(true);
  };
  const closeModal = () => {
    setIsModalOpen(false);
  };
  // console.log("   : ", scheduleData);
  // console.log('mark  : ', mark);

  //    
  const tileClassName = ({ date }) => {
    // (0)  (6)   .
    if (date.getDay() === 0 /*  */) {
      return "sunday"; //   
    }
    if (date.getDay() === 6 /*  */) {
      return "saturday"; //   
    }
    return ""; //     .
  };


  const addContent = ({ date }) => {
    //  ()   
    const contents = [];

    scheduleData.forEach((schedule) => {
      if (schedule.date === moment(date).format("YYYY-MM-DD")) {
          contents.push(
              <div id="color" className="dot" style={{ backgroundColor: schedule.color }}></div>
          );
      }
  });
  //     
    return <>
      <div className="dotWrap">
        {contents}
      </div>
    </>; 
  };

  return (
    <>
      <h3>{title}</h3>
      <Calendar
        minDetail="month"
        locale="en"
        showNeighboringMonth={false}
        tileClassName={tileClassName}
        onChange={onChange}
        value={value}
        formatDay={(locale, date) => moment(date).format("DD")}
        tileContent={addContent}
      />
      <div className="plusButtonWrap">
        <button className="plusButton" onClick={openModal}>
           
        </button>
      </div>
      {isModalOpen && <Modal closeModal={closeModal} value={value} scheduleData={scheduleData} />}
    </>
  );
};

//
const MiddleComponent = ({ value, hasSchedule, scheduleData, fetchDoData }) => {
  // console.log("  :", scheduleData);

  return (
    <>
      <div className="middleValue">{moment(value).format("YYYY MM DD")}</div>
      {hasSchedule ? (
        <>
          <Schedule scheduleData={scheduleData} value={value} />
        </>
      ) : (
        <>
          <div className="noSchedule"> .</div>
        </>
      )}
    </>
  );
};

const RightComponent = ({ title, value, hasAccount, accountData, totalMoney }) => {
  // 
  const [showInput, setShowInput] = useState(false); //     
  const [addMemo, setAddMemo] = useState("");
  const [addEvent, setAddEvent] = useState("");
  const [addPlusMoney, setAddPlusMoney] = useState("");
  const [addMinusMoney, setAddMinusMoney] = useState("");

  // 
  const [isIncome, setIsIncome] = useState(true); // true , false 

  // DB
  const handleAddExpense = () => {
    //     .
    setAddPlusMoney("");
    setAddMemo("")
    //     
    setShowInput(true); // ""    .
  };

  const addDBexpense = async () => {
    const token = localStorage.getItem("accessToken");
    const Data = {
      date: moment(value).format("YYYY-MM-DD"),
      money: addPlusMoney,
      in_ex: isIncome, // true  false 
      memo: addMemo,
    };
    // API    
    try {
      const res = await fetch("/mohani/accounts/save", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `${token}`,
        },
        body: JSON.stringify(Data), //   
      });

      if (res.ok) {
        alert("  !");
        setShowInput(false);
        window.location.reload();
      } else {
        console.error("  :", res.statusText);
        alert("  .");
      }
    } catch (error) {
      console.error(" ", error);
    }
  };

  //    
  const year = moment(value).format("YYYY");
  const month = moment(value).format("M");

  const [expend, setExpand] = useState('0');
  const [income, setIncome] = useState('0');

  const check = async () => {
    let found = false;
      for(let i=0; i<totalMoney.length; i++){
        if(totalMoney[i].year == year && totalMoney[i].month == month){
          setExpand(totalMoney[i].expenditure);
          setIncome(totalMoney[i].income);
          found = true;
          break;
        }
      }
      if(!found){
        setExpand('0');
        setIncome('0');
      }
    
  }

  useEffect(() => {
    check();
  }, [value, totalMoney])

  return (
    <>
      <h3 className="sagak">{title}</h3>

      {hasAccount ? (
        <>
          <Account accountData={accountData} value={value} />
          {/*   */}
          {showInput && (
            <div className="accountWrap">
              <div className="radioWrap">
                <input
                type="radio"
                id="income"
                name="expenseType"
                value="income"
                checked={isIncome}
                onChange={() => setIsIncome(true)}
              />
              <label for="income"></label>
              <input
                type="radio"
                id="expense"
                name="expenseType"
                value="expense"
                checked={!isIncome}
                onChange={() => setIsIncome(false)}
                />
                <label for="expense"></label>
              </div>

              <div className="AddAccountInputWrap">
                <input
                  className="AddaccountInput"
                  type="text"
                  value={addPlusMoney}
                  onChange={(e) => setAddPlusMoney(e.target.value)}
                  placeholder=""
                />
                <input
                  className="AddaccountInput"
                  type="text"
                  value={addMemo}
                  onChange={(aa) => setAddMemo(aa.target.value)}
                  placeholder=""
                />
              </div>
                <div className='miniButtonWrap'>
                  <button className='miniButton' onClick={addDBexpense}></button>
                  <button className='miniButton' onClick={() => setShowInput(false)}></button>
                </div>
            </div>
          )}
            {!showInput && (
              <div className="AddAccountButtonWrap">
            <button className="AddAccountButton" onClick={handleAddExpense}> </button> 
              </div>
            )}
          <TotalAccount totalMoney={totalMoney} value={value} income={income} expend={expend}/>
        </>
      ) : (
        <>
          <p className="sagak">  .</p>
          {/*   */}
          {showInput && (
            <div className="accountWrap">
              <div className="radioWrap">
                <input
                type="radio"
                id="income"
                name="expenseType"
                value="income"
                checked={isIncome}
                onChange={() => setIsIncome(true)}
              />
                <label for="income"></label>
                <input
                  type="radio"
                  id="expense"
                  name="expenseType"
                  value="expense"
                  checked={!isIncome}
                  onChange={() => setIsIncome(false)}
                />
                <label for="expense"></label>
              </div>

              <div className="AddAccountInputWrap">
                <input
                  type="text"
                  value={addPlusMoney}
                  onChange={(e) => setAddPlusMoney(e.target.value)}
                  placeholder=""
                  className="AddaccountInput"
                />
                <input
                  type="text"
                  value={addMemo}
                  onChange={(aa) => setAddMemo(aa.target.value)}
                  placeholder=""
                  className="AddaccountInput"
                />
              </div>

              <div className='miniButtonWrap'>
                <button className='miniButton' onClick={addDBexpense}></button>
                <button className='miniButton' onClick={() => setShowInput(false)}></button>
              </div>
            </div>
          )}
            {!showInput && (
              <div className="AddAccountButtonWrap">
                <button className="AddAccountButton" onClick={handleAddExpense}> </button> 
              </div>
            )}
          <TotalAccount totalMoney={totalMoney} value={value} income={income} expend={expend}/>
        </>
      )}
    </>
  );
};

function MainPage({ onClick }) {
  const navigate = useNavigate();
  //   
  const [value, onChange] = useState(new Date());
  //  
  const [hasSchedule, setHasSchedule] = useState(false);
  //  
  const [hasAccount, setHasAccount] = useState(false);
  //   
  const [scheduleData, setScheduleData] = useState([]);

  const [accountData, setAccountData] = useState([]);

  const [totalMoney, setTotalMoney] = useState([]);

  // // userdata
  const [userData, setUserData] = useState({
    email: "",
    memberNo: "",
    password: "",
    confirmPassword: "",
    username: "",
    phoneNumber: "",
    agree: false,
  });

  const [loading, setLoading] = useState(true);

  //   
  const isTokenValid = (token) => {
    const decodedToken = jwtDecode(token);
    const expirationTime = decodedToken.exp * 1000;
    return Date.now() < expirationTime;
  };

  // Username fetch GET
  const fetchData = async () => {
    const token = localStorage.getItem("accessToken");

    if (token && isTokenValid(token)) {
      try {
        const response = await fetch("http://localhost:3000/mohani/info", {
          headers: {
            Accept: "application/json",
            Authorization: `${token}`,
          },
        });

        if (response.ok) {
          const result = await response.json();
          setUserData((prevUserData) => ({
            ...prevUserData,
            username: result.data.username,
            memberNo: result.data.memberNo,
          }));
          // console.log(result)
          return result.data.memberNo;
        } else {
          alert(" .   .");
          //     
          navigate("/mohani/");
          localStorage.removeItem("accessToken");
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    } else {
      alert(" .   .");
      //     
      navigate("/mohani/");
      localStorage.removeItem("accessToken");
    }
  };

  // Schedule fetch GET
  const fetchDoData = async (memberNo) => {
    const token = localStorage.getItem("accessToken");

    try {
      const res = await fetch(`http://localhost:3000/mohani/${memberNo}`, {
        header: {
          Accepts: "application/json",
          Authorization: `${token}`,
        },
      });
      if (res.ok) {
        const result = await res.json();

        for (let i = 0; i < result.length; i++) {
          scheduleData.push({
            color : result[i].color,
            seq: result[i].seq,
            date: result[i].startDate,
            endDate: result[i].endDate,
            startTime: result[i].startTime,
            endTime: result[i].endTime,
            title: result[i].title,
            loc: result[i].loc,
            content: result[i].content,
          })
        }
        // console.log(result);
        // console.log(scheduleData)
        // console.log("fetch  ");
      } else {
        console.log("  ");
      }
    } catch (error) {
      console.error("error message : ", error);
    }
  };

  //    , GET
  const fetchTotalData = async (memberNo) => {
    const token = localStorage.getItem("accessToken");

    try {
      const response = await fetch(`http://localhost:3000/mohani/accounts/totalMoney/${memberNo}`, {
        headers: {
          Accept: "application/json",
          Authorization: `${token}`,
        },
      });
      if(response.ok) {
        const result = await response.json();
        setTotalMoney(result);
        // console.log(totalMoney);
      }else {
        // alert('total  get ');
      }
    } catch (error){
      console.error(' get  :', error);
    }
  }

  useEffect(() => {
    const fetchDataAndCheckSchedule = async () => {
      try {
        // fetchData     .
        if (!scheduleData.length) {
          //      fetchData     .
          const memberNo = await fetchData();
          // fetchDoData     .
          await fetchDoData(memberNo);
          // await fetchTotalData(memberNo);
        }
        return scheduleData.some((schedule) => schedule.date === moment(value).format("YYYY-MM-DD"));
      } catch (error) {
        console.error("fetch ", error);
        return false;
      }
    };

    // fetchDataAndCheckSchedule     , hasSchedule  .
    fetchDataAndCheckSchedule().then((result) => {
      setHasSchedule(result);
      // console.log('hasSchedule  : ',hasSchedule);
    });
  }, [value]);

  //  
  useEffect(() => {
    const checkAccountForDate = (date) => {
      //    true 
      return accountData.some((schedule) => schedule.date === moment(date).format("YYYY-MM-DD"));
    };
    //    hasSchedule  
    setHasAccount(checkAccountForDate(value));
  }, [value, accountData]);

      //  fetch GET
      const fetchAccountData = async (memberNo) => {
        const token = localStorage.getItem("accessToken");
    
        try {
          const res = await fetch(`/mohani/accounts/view/${memberNo}`, {
            header: {
              Accepts: "application/json",
                Authorization: `${token}`,
            },
          });
          if (res.ok) {
            const result = await res.json();
    
            for(let i=0; i<result.length; i++){
            accountData.push({
              money: result[i].money, 
              date : result[i].date, 
              in_ex: result[i].in_ex, 
              memo: result[i].memo, 
              content: result[i].content,
              idx : result[i].idx
        })
            }
            // console.log(result); //  
            // console.log(accountData)
          } else {
            console.log("  ");
          }
        } catch (error) {
          console.error("error message : ", error);
        }
      };
  
  
      useEffect(() => {
        const fetchDataAndCheckAccount = async () => {
          try {
            // fetchData     .
            if (!accountData.length) {
              //      fetchData     .
              const memberNo = await fetchData();
              // fetchDoData     .
              await fetchAccountData(memberNo);
              await fetchTotalData(memberNo);
            }
            return accountData.some((schedule) => schedule.date === moment(value).format("YYYY-MM-DD"));
          } catch (error) {
            console.error("fetch ", error);
            return false;
          }
        };
    
        // fetchDataAndCheckSchedule     , hasSchedule  .
        fetchDataAndCheckAccount().then((result) => {
          setHasAccount(result);
        });
      }, [value]);

  return (
    <div className="">
      <AppHeader userData={userData} onClick={onClick} />
      <SplitScreen leftWeight={4} middleWeight={3} rightWeight={3}>
        <LeftComponent
          onChange={onChange}
          value={value}
          scheduleData={scheduleData}
          fetchDoData={fetchDoData}
        />
        <MiddleComponent
          title=""
          value={value}
          hasSchedule={hasSchedule}
          scheduleData={scheduleData}
          onChange={onChange}
        />

        <RightComponent 
        title="" 
        value={value} 
        hasAccount={hasAccount} 
        accountData={accountData} 
        fetchAccountData ={fetchAccountData}
        totalMoney={totalMoney}        
        />
      </SplitScreen>
    </div>
  );
}
export default MainPage;
